import React from "react";
import { FaBell } from "react-icons/fa";
import "./info_bar.css";

const InfoBar = ({ classes, state, unReadMessages, handleNotifications }) => {
  const genClassesName = (classes) => {
    const names = classes.map((cls) => cls.name);
    return names.join();
  };

  const convState = (state) => {
    switch (state) {
      case 1:
        return "online";
      default:
        return "offline";
    }
  };
  const convStateStr = (state) => {
    switch (state) {
      case 0:
        return "...";
      case 1:
        return "";
      case 2:
        return "";
      case 3:
        return "";
      default:
        return "";
    }
  };
  return (
    <div className="infoBar">
      <div className="leftInnerContainer">
        <div className={`room__state ${convState(state)}`} />
        <h3>{classes ? genClassesName(classes) : ""}</h3>
        <p>{convStateStr(state)}</p>
      </div>
      <div className="rightInnerContainer">
        <div className="chat__notification" onClick={handleNotifications}>
          <FaBell />
          {unReadMessages > 0 ? (
            <span className="badge">{unReadMessages}</span>
          ) : null}
        </div>
      </div>
    </div>
  );
};

export default InfoBar;
