import React from "react";
import browser from "../utils/browser";
import { useOutsideClick } from "../utils/hook";
import "./info.css";
export default function Info(props) {
  const { open, onOutsideClick } = props;
  const refSelf = React.useRef();
  useOutsideClick(refSelf, onOutsideClick);
  React.useEffect(() => {
    if (open) refSelf.current.style.display = "flex";
    else refSelf.current.style.display = "none";
  }, [open]);
  return (
    <div>
      <div ref={refSelf} className="sideinfo">
        <p>{`${window.navigator.userAgent}`}</p>
        <p>{`MSE${browser.supportsMediaSource}`}</p>
        <p>{`API${browser.supportsNativeFullscreen}`}</p>
        <p>{`ShadowDOM${browser.supportsShadowDOM}`}</p>
        <p>{`WebAssembly${browser.supportsWASM}`}</p>
        <p>{`${browser.pip}`}</p>
        <p>{`AirPlay${browser.airplay}`}</p>
        <p>{`X5${browser.isX5}`}</p>
      </div>
    </div>
  );
}
